<template lang="html">
  <div class="main">
    <button-toggle id="btn" v-on:tog="hide" :isactive="true"></button-toggle>
    <div class="header">
      <img src="../assets/wldc.jpg" alt="">
      <p>内蒙古文化旅游业观察者  <hr>解读内蒙古地区本土民族文化， 整合民族文化旅游资源， 实现内蒙古地区优质民族文旅 IP 孵化与研究</p>

    </div>
  </div>
</template>

<script>
import ButtonToggle from './ButtonToggle'
export default {
  name: 'qr',
  methods: {
    hide(){
      var main = document.querySelector('.main')
      var btn = document.querySelector('#btn .toggle-button')
      // btn.style.transform = `rotate(0)`
      main.style.top = '-100vh'
      this.$emit('hide')
    }
  },
  components: {
    ButtonToggle
  }
}
</script>

<style lang="css" scoped>
#btn .toggle-button{
  transform: rotate(-45deg);
  cursor: pointer;
}
.main{
  max-width: 768px;
  height: 100vh;
  z-index: 1077;
  position: absolute;
  top: -100vh;
  background-color: #000;
  width: 100%;
  transition: all 0.4s ease-out;
}
.header{
  top: 40%;
  left: 50%;
  position: relative;
  max-width: 500px;
  width: 55%;
  transform: translateX(-50%) translateY(-50%);
}
img{
  width: 100%;
}
.header p{
  text-align: center;
  color: #fff;
  font-size: 14px;
  margin-top: 20px;
  word-break: break-word;
  background-color: transparent;
}

.open{
  animation: open 0.3s ease-out;
  transform: rotate(-45deg);
}
@keyframes open {
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(-45deg);
  }
}
</style>



