<template lang="html">
  <div class="gallery">
    <ul :class='{act: isActive}'>
      <li v-for="(g, index) in gs" @click='a($event, "5")'>
        <img :src="g.src" :alt="index+1" :class='{bg: isActive}'>
        <p>{{g.text}}</p>
      </li>
    </ul>
  </div>
</template>

<script>
import api from '../api/api'

export default {
  name: 'gallery',
  props: {
    gs: {
      default: function(){
        return api.index.gallery
      }
    },
    isActive: {
      default: function(){
        return true
      }
    }
  },
  methods: {
    a(event, index){
      var t = event.currentTarget
      var img = t.querySelector('img')
      var en = img.alt
      if (en === index) {
        this.$store.state.endex = index
      } else {
        this.$store.state.endex = null
      }
      this.$emit('a')
    }
  }
}
</script>

<style lang="css" scoped>
.gallery{
  /*width: 100%;*/

}
.act{
  white-space: nowrap;
}
.bg{
  background-color: #fff;
}
  ul{
    overflow-x: auto;
    padding: 20px 0;
  }
  li{
    width: 33.3%;
    /*height: 180px;*/
    box-sizing: border-box;
    padding-left: 15px;
    display: inline-block;
    vertical-align: top;
    border: 0px solid #000;
  }

  li img{
    width: 100%;
    /*background-color: #fff;*/
  }
  li p {
    padding: 8px  0px 20px;
    white-space: normal;
  }
</style>



