<template lang="html">
  <section id="main">
    <h1>你来到了胡杨林深处， 请找出哪张图片是怪兽的藏身之处</h1>
    <p>提示： 仔细观察，在这几张图片中有一张图片不太对劲。</p>
    <gallery id="gallery" v-on:a="type" :gs="gs"></gallery>
    <h1 id="c"></h1>
    <a id="eg3" class="button" @click="show">召唤勇士家族战胜怪兽</a>
    <div id="modal" @click='hide'>
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-body">
            <div id="a" class="act"></div>
            <div id="b" class="act"></div>
            <div id="c" class="act"></div>
          </div>
          <div class="modal-footer">
            <section class="footer-div footer-intr">
              <a class="link" href="/game/#/a/d">土尔扈特景区</a>
            </section>
            <section class="footer-div footer-intr">
              <a class="link" href="https://mrwh2017.github.io/map/#/">浏览土尔扈特景区地图</a>
            </section>
            <section class="footer-div footer-qr" @click.stop="slide">
              <span class="link" >关于内蒙古文旅洞察</span>
            </section>
            <section class="footer-div footer-intr">
              <a class="link" href="http://mp.weixin.qq.com/s/baxeuqorDrMEq_-9mAVIvg">关于土尔扈特民俗文化园</a>
            </section>
          </div>
        </div>
      </div>
    </div>
    <qr id="qr"></qr>
    <div class="modal-backdrop fade show" @click="hide"></div>
  </section>
</template>

<script>
import theater from 'TheaterJS'
import Gallery from './Gallery'
import Qr from './Qr'
import api from '../api/hy'
export default {
  name: 'fight',
  data: function(){
    return {
      gs: api.hy
    }
  },
  methods: {
    slide(){
      var main = document.querySelector('.main')
      var btn = document.querySelector('#btn .toggle-button')
      main.style.top = '0'
    },
    type(){
      var endex = this.$store.state.endex
      if (endex === '5') {
        this.showc()
      }
    },
    show(){
      var modal = document.querySelector('.modal-backdrop')
      var content = document.querySelector('#modal')
      content.style.display = 'block'
      modal.style.display = 'block'
      modal.style.zIndex = 1070;

      var th = theater()
      th
       .on('type:start, erase:start', function () {
         var actor = th.getCurrentActor()
         actor.$element.classList.add('actor__content')
       })
       .on('type:end, erase:end', function () {
         var actor = th.getCurrentActor()
         actor.$element.classList.remove('actor__content')
       })

       th.addActor('a', {accuracy: 1, speed: 0.3})
       th.addActor('b', {accuracy: 1, speed: 0.3})
       th.addActor('c', {accuracy: 1, speed: 0.3})
       th.addScene('a:经过一番恶战，在十三家族勇士的帮助下，你成功地击败了怪兽，拯救了女朋友',3000)
       th.addScene('b:哈哈哈， 少年好眼力。 被你发现了， 这其实就是一篇推送， 更多关于土尔扈特民俗文化园的详情信息请点击下面的链接。  by怪兽小编。',3000, () => {
         window.location.hash = '/a/d'
       })
      //  th.addScene('c:',100)
    },
    hide(){
      var modal = document.querySelector('.modal-backdrop')
      var content = document.querySelector('#modal')
      content.style.display = 'none'
      modal.style.display = 'none'
      // modal.style.zIndex = 1070;
    },
    showc(){
      var th = theater()
      th
       .on('type:start, erase:start', function () {
         var actor = th.getCurrentActor()
         actor.$element.classList.add('actor__content')
       })

       th.addActor('c', {accuracy: 1, speed: 0.3})
       th.addScene('c:怪兽：没想到我藏的这么深还是被你发现了 -_-',1000,()=>{
         var tg = document.querySelector('#eg3')
         tg.style.opacity = 1
       })
    }
  },
  mounted: function () {
  this.$nextTick(function () {
  })
  },
  components: {
    Gallery,
    Qr
  }
}
</script>

<style lang="css">
#gallery ul{
  white-space: nowrap;
}
#gallery ul li{
  padding-left: 15px;
  width: 180px;
}
#gallery ul li p{
  color: #a9ab5c;
}
#main{
  position: fixed;
  width: 100%;
  color: #fff;
  transform-style: preserve-3d;
}

#gallery, #modal{
  max-width: 768px;
  margin: 0 auto;
}
#modal{
  z-index: 1075;
  position: fixed;
  height: 100vh;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: none;
}
.modal-dialog{
  position: relative;
  max-width: 500px;
  margin: 0 auto;
}
.modal-content{
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0,0,0,.2);
  border-radius: .3rem;
  outline: 0;
}
.modal-body{
  position: relative;
  flex: 1 1 auto;
  padding: 15px;
  color: #333;
  font-size: 14px;
}
.modal-footer{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  /*justify-content: flex-end;*/
  padding: 15px;
  border-top: 1px solid #e9ecef;
  background-color: #171721;
}
.footer-div{
  height: 60px;
  width: 45%;
  display: flex;
  align-items: center;
  margin-top: 20px;
  justify-content: center;
  /*float: left;*/
  background: url(../assets/background.png) no-repeat center -5px;
  text-decoration: none;
  text-shadow: 1px 1px 0px #000;
  border-radius: 15px;
  text-align: center;
  text-decoration: none;
}
.footer-div .link{
  font-size: 14px;
  color: #fff;
}
.footer-right{
  float: right;
}
.modal-backdrop{
  position: fixed;
  height: 100vh;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1040;
  display: none;
  background-color: #000;
}
.act{
  font-size: 18px;
  line-height: 2em;
}
.actor__content--typing::after {
  content: '|';
  animation: blink 500ms infinite;
  vertical-align: top;
  font-size: 24px;
  line-height: 2em;
}
.actor__content::after {
  content: '|';
  animation: blink 500ms infinite;
  /*vertical-align: inherit;*/
  font-size: 24px;
}
@keyframes blink {
  from { opacity: 0; }
  to { opacity: 1; }
}

#eg2, #eg3{
  background: url(../assets/background.png) no-repeat center -105px;
  display: block;
  opacity: 0;
  width: 230px;
  margin: 0;
  padding: 20px 15px;
  text-align: center;
  font-family: Arial, sans-serif;
  font-size: 18px;
  letter-spacing: 0.1em;
  color: #fff;
  text-decoration: none;
  text-shadow: 1px 1px 0px #000;
  border-radius: 15px;
  background: url(../assets/stripes.png) no-repeat 0px 0px, url(../assets/background.png) no-repeat center -8px;
  background-size: cover;
  box-shadow: 2px 2px 10px #000;
  transition: all 1s ease-out;
  animation: 1s linear 0s infinite alternate ho;
  box-shadow: 4px 4px 5px #1a3a2c;
}
#eg2:hover{
  background: url(../assets/background.png) no-repeat center -5px;
  background: url(../assets/stripes.png) no-repeat 0px -200px, url(../assets/background.png) no-repeat center -8px;
  background-size: cover;
  transition: all 0.5s ease;
}
#eg3{
  margin: 0 auto;
}
@keyframes ho {
  100%{
    background: url(../assets/linear-gradient.png) no-repeat 0px 0px, url(../assets/background.png) no-repeat center -8px;
    transition: all 1s ease;
    background-size: cover;
  }
}

.show{
  opacity: 0.9;
}
h1,
h2,
p {
  font-weight: normal;
  background-color: #222932;
  max-width: 768px;
  margin: 0 auto;
  padding:0 15px;
}
#c{
  margin-bottom: 10px;
  max-width: 768px;
  text-align: justify;
}
p{
  color: #666;
}
h1 {
  letter-spacing: 0.01em;
  transform-origin: 0 0;
  line-height: 1.5em;
  /*border-top: 2px solid #181e26;*/
}
</style>



