<template>
<div class="home">
  <h1>Welcome to Inner Mongolia</h1>
  <transition name="slide-fade" mode="out-in">
    <h2 v-if="show">{{msg}}</h2>

  </transition>
  <router-link to="/a/d">
  <div id="logo">

  </div>
</router-link>
  <div id="hand" @click="hide">
    <p>点击地图收集碎片</p>
  </div>
  <div id="start">
    <router-link id="eg2" class="button" to="/a/a">点击召唤十三家族勇士</router-link>
  </div>
  <div id="map">
    <div id="left">
      <router-link to="/dome_house">
        <section class="top" @click.once="go($event, 2)">
          <img src="../assets/1.jpg" alt="迎游览 dome house 穹顶屋">
        </section>
      </router-link>
      <router-link to="/car">
        <section class="mid" @click.once="go($event, 2)">
          <img src="../assets/3.jpg" alt="欢迎游览房车营地">
        </section>
      </router-link>
      <router-link to="/visitor">
        <section class="bot" @click.once="go($event, 2)">
          <img src="../assets/5.jpg" alt="欢迎游览游客服务中心">
        </section>
      </router-link>
    </div>
    <div id="right">
      <router-link to="/tree">
        <section class="top" @click.once="go($event, 2)">
          <img src="../assets/2.jpg" alt="欢迎游览胡杨林">
        </section>
      </router-link>
      <router-link to="/thirteen">
        <section class="left" @click.once="go($event, 1)">
          <img src="../assets/4.jpg" alt="欢迎游览十三家族文化体验区">
        </section>
      </router-link>
      <router-link to="/leisure">
        <section class="right" @click.once="go($event, 4)">
          <img src="../assets/6.jpg" alt="欢迎游览休闲区和民宿区">
        </section>
      </router-link>
    </div>

  </div>

  <!-- <img src="../assets/b.jpg" alt=""> -->
  <router-view id="view">
  </router-view>
</div>
</template>

<script>
// import '../plugins/nm.js'

export default {
  name: 'search',
  data() {
    return {
      msg: '欢迎来到内蒙古土尔扈特民俗文化园',
      show: true,
      count: 0,
      left: 13,
    }
  },
  computed: {
    model: function(){
      return this.$store.state.a
    }
  },
  methods: {
    hide(event){
      var tar = event.currentTarget
      tar.style.display = 'none'
    },
    go(event, slogan) {
      var root = event.currentTarget
      var view = document.querySelector('#div .summary img')
      var hand = document.querySelector('#hand')
      var card = this.$el.querySelector('#view')
      var button = card.querySelector('.group-toggle button')
      var start = document.querySelector('#start')
      hand.style.display = 'none'


      var a = this.$store.state.a
      if (a) {
        button.classList.add('open')
        card.classList.add('slide')
      } else {
        button.classList.remove('open')
        card.classList.remove('slide')
      }
      this.$store.commit('toggle')
      // view.classList.add('big')
      // setTimeout(() => {
      //   view.classList.remove('big')
      // }, 3000)
      this.show = !this.show
      setTimeout(() => {
        this.show = true
      }, 300)

      // this.count = +window.localStorage.getItem("count") || this.count
      this.count = this.count
      if (this.count === 13 ) {
        this.count = 13
        var word = `你已集齐十三家族碎片， 召唤勇士`
        start.style.display = 'block'
      } else {
        this.count += slogan
        if (this.count === 13) {
          var word = `你已集齐十三家族碎片， 召唤勇士`
          start.style.display = 'block'
        } else {
          this.left = 13 - this.count
          var word = `你已找到 ${this.count} 个碎片，还差 ${this.left}个碎片`
        }

      }
      // window.localStorage.setItem("count", this.count)
      this.msg = word
      root.classList.add('no-border')
    }
  }
}
</script>


<style scoped >
.slide-fade-enter-active {
  transition: all .8s ease;
}

.slide-fade-leave-active {
  transition: all 1.8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}

.slide-fade-enter,
.slide-fade-leave-to {
  transform: rotateX(-30deg);
  opacity: 0.2;
}
#start{
  position: absolute;
  top: 50%;
  left: calc(50% - 135px);
  z-index: 12;
  display: none;
}
#eg2{
  background: url(../assets/background.png) no-repeat center -105px;
  display: block;
  width: 230px;
  margin: 0px 20px;
  padding: 30px 15px;
  text-align: center;
  font-family: Arial, sans-serif;
  font-size: 18px;
  color: #fff;
  text-decoration: none;
  text-shadow: 1px 1px 0px #000;
  border-radius: 15px;
  background: url(../assets/stripes.png) no-repeat 0px 0px, url(../assets/background.png) no-repeat center -8px;
  background-size: cover;
  box-shadow: 2px 2px 10px #000;
  transition: all 1s ease-out;
  animation: 1s linear 0s infinite alternate ho;
}
#eg2:hover{
  background: url(../assets/background.png) no-repeat center -5px;
  background: url(../assets/stripes.png) no-repeat 0px -200px, url(../assets/background.png) no-repeat center -8px;
  background-size: cover;
  transition: all 0.5s ease;
}
@keyframes ho {
  100%{
    background: url(../assets/linear-gradient.png) no-repeat 0px 0px, url(../assets/background.png) no-repeat center -8px;
    transition: all 1s ease;
    background-size: cover;
  }
}
.big {
  animation-name: zoomIn 1s both;
}

@keyframes zoomIn {
  0% {
    opacity: 0;
    transform: scale3d(.3, .3, .3)
  }
  50% {
    opacity: 1
  }
}

.no-border {
  border: 0px solid #fff !important;
  transition: all 1s ease-in;
}

.home {
  position: fixed;
  width: 100%;
  color: #fff;
  transform-style: preserve-3d;
}

img {
  width: 100%;
  height: 100%;
  transition: all 3s linear;
}

h2 {
  letter-spacing: 0.01em;
  transform-origin: 0 0;
  border-top: 2px solid #181e26;
}

h1,
h2 {
  font-weight: normal;
  background-color: #222932;
  max-width: 768px;
  margin: 0 auto;
  white-space: nowrap;
}

#logo {
  width: 50px;
  height: 50px;
  position: absolute;
  right: 5px;
  top: 5px;
  z-index: 5;
  background: url('../../static/logo1.png') no-repeat 50%;
  background-size: 100% auto;
}

#hand {
  width: 60px;
  height: 90px;
  position: absolute;
  top: 100px;
  right: 50px;
  z-index: 5;
  background: url('../../static/hand.svg') no-repeat 0 0;
  background-size: auto 60%;
  animation: 0.8s ease-in infinite alternate tap;
  transform: translateY(0) rotateX(0);
  transform-origin: 0 0;
  background-color: red;
}

#hand p {
  position: absolute;
  bottom: 0;
  padding: 0 5px;
}

@keyframes tap {
  0 {
    transform: translateY(0) rotateX(0);
  }
  100% {
    transform: translateY(15px) rotateX(20deg);
  }
}

#map {
  /*background: url('../assets/222.png') no-repeat;*/
  background-size: 100%;
  height: 280px;
  position: relative;
  max-width: 768px;
  margin: 0 auto;
}

#view {
  z-index: 6;
}
#left{
  position: absolute;
  top: 0;
  left: 0;
  width: 30.156%;
  height: 100%;
  z-index: 1;
}
#left section{
  width: 100%;
  border: 2px solid #fff;
  box-sizing: border-box;
  transition: all 1s linear;
}
#left .top{
  height: 22.517%;
}
#left .mid{
  height: 46.878%;
}
#left .bot{
  height: 30.795%;
}
#right{
  position: absolute;
  top: 0;
  left: 30.156%;
  width: 69.843%;
  height: 100%;
  z-index: 1;
}
#right section{
  width: 100%;
  border: 2px solid #fff;
}
#right .top{
  height: 22.517%;
}
#right .left{
  width: 65.033%;
  height: 77.704%;
  display: inline-block;
  float: left;
}

#right .right{
  width: 34.967%;
  height: 77.704%;
  display: inline-block;
  float: left;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



