<template lang="html">
  <div class="card" @click='tog'>
    <button-toggle></button-toggle>
    <section class="section" id="div">
      <gallery :gs='card[$route.params.id]' :isActive="false"></gallery>
      <!-- <list :ls='ls[$route.params.id]'></list> -->
    </section>
  </div>
</template>

<script>
import ButtonToggle from './ButtonToggle'
import Gallery from './Gallery'
import List from './List'
import api from '../api/search'

export default {
  name: 'card',
  components: {
    ButtonToggle,
    Gallery,
    List
  },
  data: function(){
    return {
      a: 1,
      dis: 155,
      card: {
        index: api.index.card,
        leisure: api.leisure.card,
        thirteen: api.thirteen.card,
        tree: api.tree.card,
        visitor: api.visitor.card,
        car: api.car.card,
        dome_house: api.dome_house.card
      },
    }
  },
  methods:{
    slide: function(){
      var card = document.querySelector('.card')
      this.a = this.$store.state.a
      card.classList.toggle('slide')
    },
    drop :function(){
      var h = window.innerHeight
      var dis = this.a? (this.dis - 170) : this.dis
      var p_h = h - dis
      var div = document.querySelector('#div')
      div.style.height = p_h + 'px'
    },
    tog: function(){
      var card = document.querySelector('.card')
      var button = document.querySelector('button')
      var a = this.$store.state.a
      if (a) {
        button.classList.add('open')
        card.classList.add('slide')
      } else {
        button.classList.remove('open')
        card.classList.remove('slide')
      }
      this.$store.commit('toggle')
      this.$emit('toggle')
    }
  },
  mounted: function(){
    setTimeout(this.drop(), 2000)
  },
  // watch: {
  //   a :function(){
  //     // setTimeout(this.drop, 1000)
  //     // this.drop()
  //     var id = this.params.id
  //     console.log(id);
  //   }
  // }

}
</script>

<style lang="css" scoped>
.card{
  width: 100%;
  margin: 0 auto;
  max-width: 768px;
  background: linear-gradient(#3b404c, #353a46) repeat;
  font-size: 14px;
  color: #fff;
  position: absolute;
  transition: all 0.3s ease-out;
  left: 50%;
  top: 350px;
  transform: translateX(-50%);
}

.open{
  animation: open 0.3s ease-out;
  transform: rotate(-45deg);
}
.slide{
  top: 65px;
}
.section{
  width: 100%;
  height: auto;
  overflow: hidden;
  overflow-y: auto;
  padding-bottom: 150px;
  margin-top: 20px;
}
.summary{
  padding: 10px 0px;
  padding-top: 0;
}
.summary img{
  width: 100%;
  height: 220px;
}
.summary p {
  word-break: break-all;
  padding: 7px 5px;
  text-align: justify;
}
</style>



