<template lang="html">
  <div class="list">
    <ul>
      <li v-for="g in ls">
        <img :src='g.src' :alt='g.alt'>
        <p>{{g.text}}</p>
      </li>
    </ul>
  </div>
</template>

<script>
import api from '../api/api'
export default {
  name: 'list',
  props: {
    ls: {
      default: function(){
        return api.index.list
      }
    }
  }
}
</script>

<style lang="css" scoped>
.list{
  width: 100%;
}
ul{
  width: 100%;
}
ul li{
  width: 100%;
}
img{
  width: 100%;
}
p{
  padding: 16px 10px;
}
</style>



