<template lang="html">
  <div class="group-toggle">
      <button class="toggle-button" @click="tog" :class="{act: isactive}">
        <span class="icon-plus"></span>
        <span class="icon-plus"></span>
      </button>
  </div>
</template>

<script>
export default {
  name: 'ButtonToggle',
  methods: {
    tog: function(){
      var button = document.querySelector('button')
      button.classList.toggle('open')
      this.$emit('tog')
    }
  },
  props: {
    isactive: {
      default: function(){
        return false
      }
    }
  }
}
</script>

<style lang="css" scoped>
  .group-toggle{
    width: 100%;
    height: 0px;
    display: inline-block;
    background-color: transparent;
    position: relative;
  }
  .icon-plus{
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background: red;
    display: inline-block;
    background: url(../assets/imgs/line.png) no-repeat center center;
    background-size: 100%;
  }
  .icon-plus:nth-of-type(1){
    transform: rotate(0deg);
  }
  .icon-plus:nth-of-type(2){
    transform: rotate(-90deg);
  }
  .toggle-button{
    border: 0;
    outline: 0;
    display: block;
    position: relative;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    transition: transform 0.3s linear;
    background-color: rgba(100  , 100, 100, 0.8);
    margin: 10px auto;
    z-index: 10;
  }
  .act{
    transform: rotate(-45deg);
  }
  .open{
    animation: open 0.3s ease-out;
    transform: rotate(-45deg);
  }
  @keyframes open {
    0%{
      transform: rotate(0deg);
    }
    100%{
      transform: rotate(-45deg);
    }
  }
</style>



