<template lang="html">
  <section id="main">
    <h1>你成功召唤十三家族勇士</h1>
    <h2>{{msg}}</h2>
    <h2></h2>
    <gallery id="gallery" v-on:a="type" :gs="gs" :isActive="false"></gallery>
    <div id="modal">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-body">
            <div id="a" class="act"></div>
            <div id="b" class="act"></div>
          </div>
          <div class="modal-footer">
                <router-link id="eg2" class="button" to="/a/b">再次召唤十三家族， 打败怪兽</router-link>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-backdrop fade show" @click="hide"></div>
  </section>
</template>

<script type="text/javascript">
// import theater from 'TheaterJS'

</script>
<script>
import api from '../api/api'
import Gallery from './Gallery'
import theater from 'TheaterJS'

export default {
  name: 'modal',
  data: function(){
    return {
      msg: '在下面的选框中找到占卜家族， 请求他占卜出女朋友的下落',
      pos: 0,
      gs: api.thirteen.gallery
    }
  },
  methods: {
    type(){
      var endex = this.$store.state.endex
      if (endex === '5') {
        this.showc()
      }
    },
    hide(event){
      var tar = event.currentTarget
      tar.style.display = 'none'
    },
    showc(){
      var modal = document.querySelector('.modal-backdrop')
      var content = document.querySelector('#modal')
      content.style.display = 'block'
      modal.style.display = 'block'
      modal.style.zIndex = 1070;

      var th = theater()

      th
       .on('type:start, erase:start', function () {
         var actor = th.getCurrentActor()
         actor.$element.classList.add('actor__content--typing')
       })
       .on('type:end, erase:end', function () {
         var actor = th.getCurrentActor()
         actor.$element.classList.remove('actor__content--typing')
       })

      th.addActor('a', { accuracy: 1, speed: 0.3 }).addActor('b', { accuracy: 1, speed: 0.3 })
      th.addScene('a:恭喜你找到了占卜家族', 100)
      th.addScene('b:在占卜家族的帮助下，你得知怪兽正藏匿在美丽的胡杨林深处， 快去打败怪兽拯救女朋友', 100, ()=>{
           setTimeout(()=>{
             var tg = document.querySelector('#eg2')
             tg.style.opacity = 1
           },1000)
         })
    }
  },
  mounted: function () {
  this.$nextTick(function () {



  })
},
  components: {
    Gallery
  }
}
</script>

<style lang="css" scoped>
#main{
  /*position: fixed;*/
  background-color: #222932;
  width: 100%;
  color: #fff;
  transform-style: preserve-3d;
  padding: 0 15px;
}

#gallery, #modal{
  max-width: 768px;
  margin: 0 auto;
}

#gallery ul{
  white-space: normal;
}
#gallery ul li{
    width: 33.3%;
    padding-left: 15px;
}
#gallery ul li:nth-of-type(3n+1){
  padding-left: 15px;
}
#modal{
  z-index: 1072;
  position: fixed;
  height: 100vh;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: none;
}
.modal-dialog{
  position: relative;
  max-width: 500px;
  margin: 30px auto;
}
.modal-content{
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0,0,0,.2);
  border-radius: .3rem;
  outline: 0;
}
.modal-body{
  position: relative;
  flex: 1 1 auto;
  padding: 15px 0;
  color: #333;
  font-size: 14px;
}
.modal-footer{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 15px;
  border-top: 1px solid #e9ecef;
  background-color: #171721;
}
.modal-backdrop{
  position: fixed;
  height: 100vh;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1040;
  display: none;
  background-color: #000;
}
.act{
  font-size: 25px;
  line-height: 2em;
  padding: 0 15px;
}
.actor__content--typing::after {
  content: '|';
  animation: blink 500ms infinite;
  vertical-align: baseline;
  font-size: 20px;
  line-height: 1.5em;
}
@keyframes blink {
  from { opacity: 0; }
  to { opacity: 1; }
}
#a,#b{
  font-size: 18px;
  letter-spacing: -0.02em;
}
#eg2{
  background: url(../assets/background.png) no-repeat center -105px;
  display: block;
  opacity: 0;
  width: 230px;
  margin: 0 auto;
  padding: 20px 15px;
  text-align: center;
  font-family: Arial, sans-serif;
  font-size: 18px;
  color: #fff;
  text-decoration: none;
  text-shadow: 1px 1px 0px #000;
  border-radius: 15px;
  background: url(../assets/stripes.png) no-repeat 0px 0px, url(../assets/background.png) no-repeat center -8px;
  background-size: cover;
  box-shadow: 2px 2px 10px #000;
  transition: all 1s ease-out;
  animation: 1s linear 0s infinite alternate ho;
  box-shadow: 4px 4px 5px #1a3a2c;
}
#eg2:hover{
  background: url(../assets/background.png) no-repeat center -5px;
  background: url(../assets/stripes.png) no-repeat 0px -200px, url(../assets/background.png) no-repeat center -8px;
  background-size: cover;
  transition: all 0.5s ease;
}
@keyframes ho {
  100%{
    background: url(../assets/linear-gradient.png) no-repeat 0px 0px, url(../assets/background.png) no-repeat center -8px;
    transition: all 1s ease;
    background-size: cover;
  }
}
.show{
  opacity: 0.5;
}
h1,
h2 {
  font-weight: normal;
  background-color: #222932;
  max-width: 768px;
  margin: 0 auto;
}
h2{
  font-size: 16px;
}
h1 {
  letter-spacing: 0.01em;
  transform-origin: 0 0;
  line-height: 2.5em;
  border-top: 2px solid #181e26;
}
</style>



